import queryString from 'query-string'
import {
  standardTransforms,
  standardDisplayOrder,
  registerTransform
} from "@nteract/transforms"
import NotebookPreview from "@nteract/notebook-preview"
import DataResourceTransform from "@nteract/transform-dataresource"

import { VegaLite1, VegaLite2, Vega2, Vega3 } from "@nteract/transform-vega"

import { PlotlyNullTransform, PlotlyTransform } from "./PlotlyTransform"

const { transforms, displayOrder } = [
  DataResourceTransform,
  VegaLite1,
  VegaLite2,
  Vega2,
  Vega3
].reduce(registerTransform, {
  transforms: standardTransforms,
  displayOrder: standardDisplayOrder
})

export default class extends React.Component {
  constructor(props) {
    super(props)

    this.state = {
      showCode: props.showCode || false,
      showCodeOutput: props.showCodeOutput || false,
      setUrl: props.setUrl || 'hidden',
      optionsState: props.optionsState || '2'
    }

    if (typeof window === 'undefined') return

    const query = queryString.parse(window.location.search)
    
    if (query && Object.prototype.hasOwnProperty.call(query, 'code')) { // eslint-disable-line
      if (query.code === 'hidden') {
        this.state = {
          setUrl: 'hidden',
          optionsState: '2'
        }
      } else if (query.code === 'shown') {
        this.state = {
          showCode: true,
          showCodeOutput: true,
          setUrl: 'shown',
          optionsState: '1'
        }
      } else if (query.code === 'output') {
        this.state = {
          showCode: false,
          showCodeOutput: true,
          setUrl: 'output',
          optionsState: '3'
        }
      }
    }
  }

  componentWillReceiveProps(props) {
    if (typeof props.showCode !== 'undefined') this.setState({ showCode: props.showCode })
    if (props.setUrl === 'shown') this.setState({ showCode: true, optionsState: '1' })
    if (props.setUrl === 'hidden') this.setState({ showCode: false, showCodeOutput: false, optionsState: '2' })
    if (props.setUrl === 'output') this.setState({ showCode: false, showCodeOutput: true, optionsState: '3' })
  }

  render() {
    const { content, type } = this.props
    const { showCode, showCodeOutput, optionsState } = this.state

    const changeShowCode = (e) => {
      e.preventDefault()
      if (e.target.value === '1') this.setState({ showCode: true, showCodeOutput: true, optionsState: 1 })
      if (e.target.value === '2') this.setState({ showCode: false, showCodeOutput: false, optionsState: 2 })
      if (e.target.value === '3') this.setState({ showCode: false, showCodeOutput: true, optionsState: 3 })
    }

    const style = `
      ${!showCode ? `
        .input-container {
          display: none!important;
        }
      ` : ''}

      ${!showCodeOutput ? `
        .nteract-display-area-stdout {
          display: none!important;
        }

        .nteract-display-area-stderr {
          display: none!important;
        }
      ` : ''}
    `

    console.log(showCode, showCodeOutput, style)


    return (
      <div>
        Code:{'  '}
        <select onChange={changeShowCode} value={optionsState}>
          <option value={2} defaultValue={optionsState === 2}>Hidden</option>
          <option value={1} defaultValue={optionsState === 1}>Shown</option>
          <option value={3} defaultValue={optionsState === 3}>
            Hidden, but show code outputs
          </option>
        </select>

        <NotebookPreview
          notebook={content}
          displayOrder={displayOrder}
          transforms={transforms}
        />

        <style jsx global>{style}</style>
      </div>
    )
  }
}



